	import taskItem from '@/pages/task/taskItem.vue';
	export default {
		components: {
			taskItem
		},
		data() {
			return {
				taskShow:true,
				task_basic_data:{
					"msg": "success",
					"shareCount": 0,
					"userPoints": "10",
					"code": 0,
					"sign": "0  ",
					"coutinueMark": "0"
				},
				task_list :[{list_title:"今日签到",
								list_award:"+10金币/个",
								content:"每次点击签到奖励10个",
								btn:"去签到",
								id:0
								},{list_title:"邀请用户注册",
								list_award:"+100金币/个",
								content:"邀请一位好友注册奖励100个",
								btn:"去完成",
								id:1
								},{list_title:"分享专题",
								list_award:"+5金币/个",
								content:"每次首次分享专题奖励5个",
								btn:"去分享",
								id:5
								}],
				
			}
		},
		onShow:function (options){
			var  uid = '';
			if(this.$store.state.userInfo.userId){
				uid = this.$store.state.userInfo.userId;
			}
			uni.request({
				url: this.websiteUrl +'/api/task/list?userId=' + uid, //仅为示例，并非真实接口地址。
				success: (res) => {
					if(res.data.status == 0){
						this.task_basic_data = res.data;
						this.taskShow = false;
						this.$nextTick(()=>{
						 this.taskShow = true;
						})
					}
				}
			});
				},
		methods: {
			gotoMyInvitation(){
				uni.navigateTo({
					url: '../../../pages/task/my_invitation'
				})
			},
			gotoMyExchange(){
				uni.navigateTo({
					url: '../../../pages/task/my_exchange'
				})
			},
			gotoMyCoinList(){
				uni.navigateTo({
					url: '../../../pages/task/my_coin'
				})
			},
			toChangData(data){
				this.task_basic_data.continueMark = data.coutinueMark;
				this.task_basic_data.point = data.userPoints;
			},
            toPrize(){
                uni.navigateTo({
                    url: '/pages/luckDraw/luckDraw',
                    animationType: 'slide-in-right',
                    animationDuration: 300
                });
            }
		}
	}
